<template>
  <v-container>
    <v-row
      justify="center"
    >
      <v-col
        v-for="{ id, title, src } in pictures"
        :key="id"
        cols="12"
        xl="3"
        lg="3"
        md="4"
        sm="6"
        xs="12"
      >
        <v-card>

          <v-img
            :src="src"
            :height="imageHeight"
          >
          </v-img>

          <v-card-title>{{ title }}</v-card-title>

          <v-card-actions>

            <v-btn
              text
              :to="linkPaintingDetail(id)"
            >
              {{ $t('gallery.more-button') }}
            </v-btn>

            <v-spacer></v-spacer>

            <v-btn
              icon
            >
              <v-icon>mdi-heart</v-icon>
            </v-btn>

            <v-btn
              icon
            >
              <v-icon>mdi-bookmark-plus</v-icon>
            </v-btn>

            <v-btn
              icon
            >
              <v-icon>mdi-share-variant</v-icon>
            </v-btn>

          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  name: 'Gallery',
  computed: {
    imageHeight () {
      switch (this.$vuetify.breakpoint.name) {
        case 'xl': return 600;
        case 'lg': return 360;
        case 'md': return 360;
        case 'sm': return 420;
        case 'xs': return 520;
        default: return 360;
      }
    },
  },
  methods: {
    linkPaintingDetail: function (id) {
      return `/gallery/${ id }`;
    },
  },

  data: () => ({
    pictures: [
      { id:'0', title: 'Eiszeit', src: require('@/assets/eiszeit.png'), },
      { id:'1', title: 'Bunter Sturm', src: require('@/assets/bunter_sturm.png'), },
      { id:'2', title: 'Seifenblasen' , src: require('@/assets/seifenblasen.png'), },
      { id:'3', title: 'Saison Der Liebe', src: require('@/assets/saison_der_liebe.png'), },
      { id:'4', title: 'Hingebung', src: require('@/assets/0-0.jpg'), },
    ]
  }),
}
</script>