import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Gallery from '../views/Gallery.vue'
import PaintingDetail from '../views/PaintingDetail.vue'
import Exhibitons from '../views/Exhibitions.vue'
import AboutMe from '../views/AboutMe.vue'
import Contact from '../views/Contact.vue'
import Signup from '../views/Signup.vue'
import AddPainting from '../views/AddPainting'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
  },
  {
    path: '/gallery',
    name: 'Gallery',
    component: Gallery,
  },
  {
    path: '/gallery/:id',
    name: 'Painting Detail',
    component: PaintingDetail,
  },
  {
    path: '/exhibitons',
    name: 'Exhibitons',
    component: Exhibitons,
  },
  {
    path: '/about_me',
    name: 'About Me',
    component: AboutMe,
  },
  {
    path: '/contact',
    name: 'Contact',
    component: Contact,
  },
  {
    path: '/kJaE2dn5KJN/manage',
    name: 'Signup',
    component: Signup,
  },
  {
    path: '/manage/add_painting',
    name: 'Add Painting',
    component: AddPainting,
  }
]

const router = new VueRouter({
  routes,
  scrollBehavior() {
    return {x: 0, y: 0}
}
})

export default router
