<template>
  <v-container>
    <v-stepper
      v-model="step"
      vertical
    >
      <v-stepper-step :complete="step > 1" step="1">Bilder</v-stepper-step>
      <v-stepper-content step="1">
        <v-card>
          <v-card-text>
            <v-row>
              <v-col>
                <v-file-input
                  v-model="images"
                  accept="image/png, image/jpeg"
                  chips
                  counter
                  label="Bilder Auswählen"
                  multiple
                  prepend-icon="mdi-folder-multiple-image"
                  show-size
                  @change="addImagesToPainting"
                ></v-file-input>
              </v-col>
            </v-row>

            <v-row
              v-if="addedPainting.imageURLs.length > 0"
            >
              <v-col>
                Bitte legen Sie die Reihenfolge der Bilder fest. Das als erstes angewählte Bild wird als Vorschaubild verwendet. 
              </v-col>
            </v-row>

            <v-row
              v-if="addedPainting.imageURLs.length > 0"
            >
              <v-col>
                <v-btn
                  :disabled="sortedImageURLs.length < 1"
                  text
                  @click="sortedImageURLs = []"
                >
                  Neu Anordnen
                </v-btn>
              </v-col>
            </v-row>

            <v-row>
              <v-col
                v-for="url in addedPainting.imageURLs"
                :key="url"
                cols="12"
                lg="2"
                sm="4"
                class="pb-8"
              >
                <v-card>

                  <v-img
                    contain
                    :src="url"
                    :lazy-src="url"
                  >
                    <template v-slot:placeholder>
                      <v-row
                        class="fill-height ma-0"
                        align="center"
                        justify="center"
                      >
                        <v-progress-circular indeterminate></v-progress-circular>
                      </v-row>
                    </template>
                  </v-img>

                  <v-btn
                    v-if="imageNumber(url) === undefined"
                    absolute
                    bottom
                    fab
                    small
                    right
                    :value="url"
                    @click="pushImage(url)"
                  > 
                    <v-icon>mdi-plus</v-icon>
                  </v-btn>

                  <v-btn
                    v-else
                    absolute
                    bottom
                    exact
                    fab
                    small
                    right
                  > 
                    {{ imageNumber(url) + 1 }}
                  </v-btn>
                </v-card>
              </v-col>
            </v-row>

            

          </v-card-text>

          <v-card-actions>
            <v-btn
              text
              @click="step++"
            >
              Weiter
            </v-btn>
          </v-card-actions>
        </v-card>

      </v-stepper-content>

      <v-stepper-step :complete="step > 2" step="2">Allgemeine Daten</v-stepper-step>
      <v-stepper-content step="2">

      </v-stepper-content>

      <v-stepper-step step="3">Deutsche Übersetzung</v-stepper-step>
      <v-stepper-content step="3">

      </v-stepper-content>

    </v-stepper>
  </v-container>  
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

export default {
  computed: {
    ...mapGetters(['addedPainting']),
    
  },
  created () {
    this.addPainting({
      imageURLs: [],
    });
  },
  data () {
    return {
      step: 1,
      images: [],
      sortedImageURLs: [],
    }
  },
  methods: {
    ...mapActions(['addPainting', 'addImagesToPainting']),

    pushImage (imageURL) {
      this.sortedImageURLs.push(imageURL);
    },

    imageNumber (imageURL) {
      const index = this.sortedImageURLs.findIndex(element => element === imageURL);
      if (index === -1) {
        return;
      }
      return index;
    },
  },
}
</script>